.matching-stat {
  background-color: yellow;
}

.header {
  background-color: rgb(22, 19, 19);
  height: 50px;
  width: 100vw;
  color: rgb(122, 4, 4);
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 1.5em;
  margin-block-start: 0.83em;
  margin-block-end: 0.83em;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
  font-weight: bold;
}

.filter {
  display: flex;
  justify-content: center;
  align-items: center;
}

.filter select {
  padding: 5px;
}

.filterSelectMain {
  display: flex;
  height: 50px;
}

.filter ul {
  gap: 10px;
}

.resDiv {
  display: flex;
  justify-content: center;
  align-items: center;
}

.resDiv ul {
  list-style-type: none;
}
